<template>
	<div id="talkingNow">
		<v-header></v-header>
		<img v-lazy="$route.params.img" class="detailImg" />
		<div class="talking">
			<div class="talking-top">
				<p></p>
				<p><img src="/dist/image/home/subhome/star_good.png" v-for="n in num" @click="change(n)" /><img src="/dist/image/home/subhome/star_bad.png" v-for="n in (5-num)" @click="change(num+n)"/></p>
				<p>{{num | level}}</p>
			</div>
			<div class="talking-bottom">
				<ul>
					<li @click="add('')"></li>
					<li @click="add('')"></li>
					<li @click="add('')"></li>
					<li @click="add('')"></li>
					<li @click="add('')"></li>
					<li @click="add('')"></li>
				</ul>
				<div class="text-wrap">
					<textarea placeholder="" v-model="value"></textarea>
					<span>{{count}}/50</span>
				</div>
				<!--<div class="img-wrap">
					<div class="addImg">
						<img src="/dist/image/talkingnow/camera.png" />
						<span></span>
					</div>
				</div>-->
			</div>
		</div>
	</div>
</template>

<script>
	import Header from "./header.vue";
	export default{
		components:{
			"v-header":Header
		},
		data(){
			return {
				num:0,
				count:0,
				value:""
			}
		},
		methods:{
			change(n){
				this.num = n;
			},
			add(str){
				var temp = this.value;
				temp += str;
				this.value = temp;
			}
		},
		filters:{
			level:function(val){
				switch(val){
					case 0:
					return "";
					case 1:
					return "";
					case 2:
					return "";
					case 3:
					return "";
					case 4:
					return "";
					case 5:
					return "";
				}
			}
		},
		watch:{
			"value":function(){
				
				this.count = this.value.length;
				if(this.count > 50){
					var temp = this.value;
					temp = temp.substring(0,50);
					this.value = temp;
				}
			}
		},
		beforeRouteEnter(to,from,next){
			if(localStorage.getItem("info")){
				if(JSON.parse(localStorage.getItem("info")).time < new Date().getTime()){
					localStorage.removeItem("info");
					sessionStorage.removeItem("userid");
					sessionStorage.removeItem("usertoken");
					next(vm => {
						vm.$router.push("/");
					});
				}else{
					var time = new Date();
					time = time.getTime() + 3*24*60*60*1000;
					var obj = JSON.parse(localStorage.getItem("info"));
					obj.time = time;
					localStorage.setItem("info",JSON.stringify(obj));
					sessionStorage.setItem("userid",JSON.parse(localStorage.getItem("info")).userid);
					sessionStorage.setItem("usertoken",JSON.parse(localStorage.getItem("info")).usertoken);
					next();
				}
			}else{
				next(vm => {
					vm.$router.push("/");
				});
			}
		},
		activated(){
			wx.hideMenuItems({
			  menuList: ["menuItem:copyUrl","menuItem:readMode","menuItem:openWithQQBrowser","menuItem:openWithSafari","menuItem:share:qq","menuItem:share:weiboApp","menuItem:favorite","menuItem:share:facebook","menuItem:share:QZone"] // menu3
			});
		}
	}
</script>

<style>
</style>


